import axios from '@/api/axiosApi'
import { localUser } from '@/assets/js/local'
import { isIOS } from '@/assets/js/brower'
import VHeader from '@/components/v-header/v-header'
import ThirdLogin from './thirdLogin'
import nativeBridge from '@/assets/js/nativeBridge'
import md5 from 'js-md5'
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      clickBack: false,
      phone: '',
      password: '',
      iosShow: true
    }
  },
  components: {
    VHeader, ThirdLogin
  },
  computed: {
    ...mapGetters({
      transitionName: 'loginAnmation'
    })
  },
  methods: {
    // isHasWeiChat () {
    //   window.webkit.messageHandlers.NativeMethod.postMessage({'methodName': 'hasWechat'})
    // },
    beforeLeave (el) {
      // 苹果手机通过侧滑返回(即不点击返回)，则直接隐藏
      if (isIOS() && !this.clickBack) {
        el.style.opacity = 0
      }
    },
    toRegister () {
      this.$router.replace({
        path: '/user/login/register'
      })
    },
    toPhoneAuth () {
      this.$router.replace('/user/login/forgetPass')
    },
    login () {
      if (this.phone === '') {
        this.$vux.toast.text('请输入手机号', 'middle')
        return
      }
      if (this.password === '') {
        this.$vux.toast.text('请输入密码', 'middle')
        return
      }
      const _self = this
      axios.post(_self, '/v1/phoneLogin', {
        phone: _self.phone,
        password: _self.password
      }, (data) => {
        nativeBridge.setJPushAlias(data.id)
        data.password = md5(_self.password)
        localUser.set(data)
        _self.$router.replace('/')
        _self.$store.dispatch('initJIM')
        _self.$store.commit('SET_LOGIN_ANMATION', '')
      })
    }
  }
}



