
import { localUser } from '@/assets/js/local'
import Divider from 'vux/src/components/divider/index.vue'

import nativeBridge from '@/assets/js/nativeBridge'
import axios from '@/api/axiosApi'

export default {
components: {
Divider
},
methods: {
wechatLogin () {
nativeBridge.wechatLogin(this, (arg) => {
localUser.setItem('openid', arg.openid)
localUser.setItem('sex', arg.sex)
localUser.setItem('nickname', arg.nickname)
localUser.setItem('headUrl', arg.headimgurl)
const _self = this
axios.post(_self, '/v1/weChatLogin/' + arg.openid, null, (data) => {
nativeBridge.setJPushAlias(data.id)
localUser.set(data)
_self.$router.replace('/')
_self.$store.dispatch('initJIM')
_self.$store.commit('SET_LOGIN_ANMATION', '')
}, () => {
_self.$router.replace('/user/login/bindPhone')
})
})
}
}
}



